.header-color{
  background: #fc00ff;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #00dbde, #fc00ff);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #00dbde, #fc00ff); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.landing-grid{
  background: #fc00ff;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #00dbde, #fc00ff);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #00dbde, #fc00ff); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  position: relative;
  top:0;
  left:0;
  min-width: 75%;
  height: 100%; 
  text-align: center;
  margin:auto;
}
image-set1{
  height: 10px;
  vertical-align: middle;
  padding-top:3cm;
  border-radius:50%;
}
.image-set{
  height: 250px;
  vertical-align: middle;
  padding-top: 1cm;
  border-radius: 100%;
  
  }
.banner{
  background-color: black;
  opacity: .8;
  min-width: 50%;
  margin:auto;
  border-radius: 10px;
}
.banner h1{
  font-size: 50px;
  font-weight: bold;
  color:white;
}
.banner hr{
  border-top: 3px solid white;
  width: 50%;
  margin:auto;
}
.banner p{
  color:white;
  font-size: 20px;
  padding: 1cm;
}
.main-body {
  min-height: 100%;
  min-width:100%;
}
.contact-body {
  margin:auto;
  position: relative;
  width: 100%; 
  min-height:100%;
  /* z-index: -1; */
  
  justify-content: center; 
  padding-top: 2cm; 
  background: #fc00ff;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #00dbde, #fc00ff);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #00dbde, #fc00ff); 
  opacity: 0.8;
  


}
.particles{
  position: fixed;
  top:0;
  z-index:-1;
  right:0;
  bottom: 0;
  left: 0;

}
.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  color:black;
  
  
  margin-bottom:30%;
  background: #fc00ff;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #00dbde, #fc00ff);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #00dbde, #fc00ff); 
}
.iconColor {
  color:black;
}

.contact-grid hr {
  border-top:5px dotted black;
  width:70%;
  margin:auto;

}
